@font-face {
  font-family: formula;
  src: url("./Formula1-Regular.ttf");
}

.main {
  font-family:formula;
  display: flex;
  justify-content: center;
  width: 100vw;
  height: auto;
  min-height: 100vh;
  background-color: #15151e;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  max-width: 750px;
  align-content: flex-start;
  height: fit-content;

}

.main:first-child {
}

.driver {
  width: 90vw;
  height: 90vw;
  max-height: 350px;
  max-width: 350px;
  display: flex;
  justify-content: start;
  align-items: end;
  flex-wrap:wrap;
}

.driver-image {
  width: 92.5%;
  height: 82.5%;
  object-fit: cover;
  cursor: pointer;
  box-shadow: 0px 0px 5px 0px rgba(255, 24, 1, 0.5);
  transition: 0.5s;
}

.small {
  width: 35vw;
  height: 35vw;
  max-height: 175px;
  max-width: 175px;
}

.border-container {
  /* width: 90%;
  height: 90%; */
  max-height: 329px;
  max-width: 329PX;
  position: absolute;
  top: 0px;
  right: 0px;
}

.drivers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 88%;
  max-width: 350px;
  align-content: flex-start;
  /* gap: 20px; */
  border-top: 4px rgba(255, 24, 1, 0.5) solid;
  border-right: 4px rgba(255, 24, 1, 0.55) solid;
  border-top-right-radius: 15px;
}

.driver-text {
  position: relative;
  bottom: 10px;
  color:white;
}
